nav {
  z-index: 10000;
  background-color: #000;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#nav-dots {
  justify-content: center;
  align-items: center;
  width: 35%;
  min-width: 100px;
  max-width: 200px;
  display: flex;
  position: relative;
}

#nav-dots ol {
  width: 100%;
  padding-top: 100%;
  position: relative;
}

#nav-dots li {
  background-color: #fff;
  border-radius: 100%;
  width: 7%;
  height: 7%;
  position: absolute;
  transform: translate(-50%, -50%);
}

#nav-dots li.focus {
  width: 10%;
  height: 10%;
}

#nav-dots li.wrong {
  background-color: #ff8c8c;
}

#nav-dots li:nth-child(n+1) {
  top: 0;
}

#nav-dots li:nth-child(n+4) {
  top: 50%;
}

#nav-dots li:nth-child(n+7) {
  top: 100%;
}

#nav-dots li:nth-child(3n+1) {
  left: 0;
}

#nav-dots li:nth-child(3n+2) {
  left: 50%;
}

#nav-dots li:nth-child(3n+3) {
  left: 100%;
}

#nav-pattern {
  z-index: 11000;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  position: absolute;
}

#nav-dots img {
  -webkit-user-select: none;
  user-select: none;
  width: 105%;
  position: absolute;
}

@keyframes nav-shrink {
  0% {
    background-color: #000;
    border-radius: 0%;
    outline-color: #0000;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  100% {
    background-color: #0000004d;
    border-radius: 200px;
    outline-color: #fff;
    width: 65px;
    height: 65px;
    top: 10px;
    left: 10px;
  }
}

nav.small {
  outline-style: none;
  animation: 1.5s cubic-bezier(.75, 0, .28, 1) 1.2s forwards nav-shrink;
}

nav.outline {
  outline-width: 1px;
  outline-style: solid;
}

@keyframes dot-shrink {
  0% {
    width: 35%;
    min-width: 100px;
    max-width: 200px;
  }

  100% {
    width: 35px;
    min-width: 35px;
  }
}

nav.small #nav-dots {
  animation: 2s cubic-bezier(.57, 0, .58, 1) .2s forwards dot-shrink;
}

@keyframes dot-disappear {
  0% {
    background-color: #fff;
  }

  100% {
    background-color: #0000;
  }
}

nav.small #nav-dots li {
  animation-name: dot-disappear;
  animation-duration: 1.5s;
  animation-timing-function: ease-in-out;
  animation-delay: .2s;
  animation-fill-mode: forwards;
}

nav > a.home {
  width: 100%;
  height: 100%;
  position: absolute;
}

#nav-pattern {
  justify-content: center;
  align-items: center;
  display: flex;
}

#nav-pattern > img {
  opacity: 0;
  width: 36.75%;
  min-width: 105px;
  max-width: 210px;
  transition: opacity .3s;
  position: absolute;
  inset: auto;
}

#nav-pattern > .nav-link {
  margin: 10% 5% auto;
}

#nav-pattern > .nav-link > img {
  opacity: .7;
  width: 50px;
}

#nav-pattern > .nav-link:hover + img {
  opacity: 1;
}

